.grid{
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(250px,1fr));
    margin-top: 1rem;
    margin-bottom: 1rem;
    grid-gap: 1rem;
    
  }
  .logo{
    width:7rem;
    margin: 1rem;
    justify-self: start;
    filter: grayscale(100%) invert(1);

    
  }
 .nav_end{
    font-weight: bold;
    margin: 1rem;
    display: inline-block;

 }
 .nav_end_content{
    margin: 0.5rem;
    display: inline-block;

 }
  
.dropdown {
    position: relative;
    display: inline-block;
    margin: 1REM;
    font-weight: bold;
      }
  
  .dropdown-content {
    display: none;
    position: absolute;
    background-color: #406783;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
  }
  
  .dropdown-content a {
    color: white;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
  }
  
  .dropdown-content a:hover {background-color: #a2a2a2;}
  
  .dropdown:hover .dropdown-content {display: block;}
  